$background1: #414141;
$background2: #262626;
$border: #000;
$breakpoint: #822;
$sel: #05a2ff;
$font-color: #fff;
$close1: #fff;
$close2: #ff0000;

div#editorcontainer {
  width: calc(100% - 300px);
  height: calc(100% - 32px);
  position: absolute;
  left: 300px;
  top: 32px;
  background-color: $background1;
  box-shadow: 1px -1px 0px $border inset, -1px -1px 0px $border inset;
  -webkit-box-shadow: 1px -1px 0px $border inset, -1px -1px 0px $border inset;
  & .breakpoints {
    width: .8em; }

  & .breakpoint {
    color: $breakpoint; }

  & .CodeMirror {
    width: calc(100% - 2px);
    height: calc(100% - 31px); }

  & div#editorfiles {
    background: $background2;
    width: calc(100% - 2px);
    height: 30px;
    margin: 0px;
    padding: 0px;
    border-right: 1px solid $border;
    border-left: 1px solid $border;
    overflow-y: hidden;
    overflow-x: auto;
    -webkit-box-shadow: 0px -1px 0px $border inset;
    box-shadow: 0px -1px 0px $border inset;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;

    & div#editorfilescontainer {

      width: auto;
      overflow: hidden;
      width: auto;
      height: 100%;
      margin: 0px;
      padding: 0px;

      & div.openedfile {
        background-color: $background2;
        width: 200px;
        height: 29px;
        margin: 0px;
        padding: 0px;
        position: relative;
        border-right: 1px solid $border;
        border-bottom: 1px solid $border;
        border-top: none;
        float: left;
        overflow: hidden;
        z-index: 1000;
        position: relative;
        cursor: pointer;

        &.selected {
          background-color: $background1;
          border-bottom: 1px solid $background1; }

        &.edited {
          -webkit-box-shadow: 0px -3px 0px $sel inset;
          box-shadow: 0px -3px 0px $sel inset;

          &.selected {
            border-bottom: 1px solid $sel;
            border-bottom-left-radius: 1px;
            border-bottom-right-radius: 1px; } }

        & p {
          margin: 0px;
          position: relative;
          top: 50%;
          -webkit-transform: translate(0%, -50%);
          -ms-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
          color: $font-color;
          text-align: center; }

        & i {
          position: absolute;
          right: 6px;
          top: 50%;
          -webkit-transform: translate(0%, -50%);
          -ms-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
          font-size: 16px;
          color: $close1;
          border-radius: 4px;
          cursor: pointer;

          &:hover {
            color: $close2; } } } } }

  & div.MineEditor {
    height: calc(100% - 32px);
    width: calc( 100% - 2px); } }

// Theme
.cm-s-mtheme {

  &.CodeMirror {
    border: 1px solid #000;
    border-top: none;
    background-color: #414141;
    color: #d6d5d4; }

  div.CodeMirror-selected {
    background: #777; }

  .CodeMirror-line {


    &::selection {
      background-color: #555; }


    > span {
      &::selection, > span::selection {
        background-color: #555; } }


    &::-moz-selection {
      background-color: #555; }


    > span {
      &::-moz-selection, > span::-moz-selection {
        background-color: #555; } } }


  .CodeMirror-gutters {
    background-color: #414141;
    border-right: 0px; }


  .CodeMirror-guttermarker {
    color: #db2d20; }


  .CodeMirror-guttermarker-subtle, .CodeMirror-linenumber {
    color: #b9b9b9; }


  .CodeMirror-cursor {
    border-left: 1px solid #807d7c; }

  span {
    &.cm-comment {
      color: rgb(106, 106, 106);
      font-style: italic; }

    &.cm-atom {
      color: #14ff00; }

    &.cm-number {
      color: rgb(0, 255, 255); }

    &.cm-property, &.cm-attribute {
      color: #01a252; }

    &.cm-keyword {
      color: #db2d20; }

    &.cm-string {
      color: #01a252; }

    &.cm-variable {
      color: #eb00ff; }

    &.cm-variable-2 {
      color: #01a0e4; }

    &.cm-def {
      color: #ffb800; }

    &.cm-bracket {
      color: #a600ff; }

    &.cm-tag {
      color: #db2d20; }

    &.cm-link {
      color: #a16a94; }

    &.cm-error {
      background-color: #db2d20;
      color: #807d7c; }

    &.cm-operator {
      color: #ffff00; }

    &.cm-extra1 {
      color: #5cffc6; }

    &.cm-extra2 {
      color: #905acd; }

    &.cm-extra3 {
      color: #d0ff00; }

    &.cm-style-black {
      background-color: #000000;
      border: 1px solid #898c87; }

    &.cm-style-dark_blue {
      background-color: #0000AA; }

    &.cm-style-dark_green {
      background-color: #00AA00; }

    &.cm-style-dark_aqua {
      background-color: #00AAAA; }

    &.cm-style-dark_red {
      background-color: #AA0000; }

    &.cm-style-dark_purple {
      background-color: #AA00AA; }

    &.cm-style-gold {
      background-color: #FFAA00;
      color: #000; }

    &.cm-style-gray {
      background-color: #AAAAAA;
      color: #000; }

    &.cm-style-dark_gray {
      background-color: #555555; }

    &.cm-style-blue {
      background-color: #5555FF;
      color: #000; }

    &.cm-style-green {
      background-color: #55FF55;
      color: #000; }

    &.cm-style-aqua {
      background-color: #55FFFF;
      color: #000; }

    &.cm-style-red {
      background-color: #FF5555;
      color: #000; }

    &.cm-style-light_purple {
      background-color: #FF55FF;
      color: #000; }

    &.cm-style-yellow {
      background-color: #FFFF55;
      color: #000; }

    &.cm-style-white {
      background-color: #FFFFFF;
      color: #000; }

    &.cm-style-reset {
      border: 1px solid #ff0000; }

    &.cm-style-bold {
      font-weight: bold; }

    &.cm-style-underline {
      text-decoration: underline; }

    &.cm-style-italic {
      font-style: italic; }

    &.cm-style-strikethrough {
      text-decoration: line-through; }

    &.cm-style-obfuscated {
      background-color: linear-gradient(-90deg, #000, #0000AA, #00AA00, #00AAAA, #AA0000); }

    &.cm-command {
      color: #ff9900; }

    &.cm-variable-3 {
      color: #cc00ff; }

    &.cm-comment2 {
      color: #0bb8d6;
      font-style: italic; }

    &.cm-comment2_annountion {
      color: #cc00ff;
      font-weight: bold;
      font-style: normal; }

    &.cm-comment2_htmltag {
      color: #5dd1e6;
      font-weight: bold; }

    &.cm-comment2_annountion_value {
      color: #ffbf00;
      font-style: normal; } }

  .CodeMirror-activeline-background {
    background: #2F2F2F; }

  .CodeMirror-matchingbracket {
    text-decoration: underline;
    color: white !important; } }
